.page-template .site-main,
.post-template .site-main {
    margin-top: 70px;
}

.site-nav-left {
    padding: 6px 0;
    margin: 0;
}

.site-nav-logo {
    margin-right: 0;
    margin-left: 22px;
    padding: 0;
}

.site-nav-logo img {
    height: 47px;
    margin: 5px 0;
    max-width: 162px;
    transition: height 0.3s;
}

.post-full-content,
.post-card-excerpt {
    font-family: 'Open Sans Hebrew', 'Open Sans', 'Helvetica Neue', 'Helvetica', Arial, sans-serif !important;
}

.site-nav-main.outer {
    padding: 0 35px;
    background: #fff;
    color: #333;
    font-family: 'Open Sans Hebrew', 'Open Sans', 'Helvetica Neue', 'Helvetica', Arial, sans-serif !important;
    border-bottom: 1px solid #dadada;
}

.site-nav-main.outer.nav-post-title-active {
    padding: 0 35px;
}

.site-nav-main.outer.nav-post-title-active .site-nav-logo img {
    height: 34px;
}

.site-nav-main.outer .site-nav {
    height: 70px;
    transition: height 0.3s;
}

.site-nav-main.outer.nav-post-title-active .site-nav {
    height: 53px;
}

.site-nav-main.outer .nav li a {
    color: #333;
    font-size: 16px;
    font-weight: 700;
    line-height: 18px;
    opacity: 1;
    padding: 12px 14px;
}

.site-nav-main.outer .inner {
    max-width: 100%;
}

.site-nav-main .site-nav-left-wrapper::after {
    content: none;
}

.site-nav-content ul li {
    margin-top: 15px;
    letter-spacing: 1px;
    vertical-align: top;
    transition: margin 0.2s;
}

.site-nav-content ul li.nav-blvg {
    letter-spacing: 0;
}

.site-nav-content ul li.nav-hrshmh-l-dkvnym {
    letter-spacing: 0;
}

.site-nav-left .nav li:last-of-type {
    padding-right: 0;
}

.site-nav-right .nav-prsvm-nks a {
    border-radius: 4px;
    color: #fff !important;
    font-weight: 400 !important;
    background-color: #2e8ddd;
    border-color: #2e8ddd;
    padding: 8.2px 12px !important;
    font-size: 14px !important;
    line-height: 1.42857143;
}

.social-link svg,
.rss-button svg {
    fill: #aaa;
}

.nav-post-title {
    color: #333;
}

blockquote {
    font-style: italic;
}

::-webkit-scrollbar {
    width: 10px;
    height: 6px;
    transition: all 0.3s ease-in-out;
}

::-webkit-scrollbar-button {
    width: 0;
    height: 0;
}

::-webkit-scrollbar-track {
    border: none;
    transition: all 0.3s;
    border-radius: 0;
}

::-webkit-scrollbar-thumb {
    background: #d9d9d9;
    border: 0 none #fff;
    border-radius: 50px;
}

::-webkit-scrollbar-corner {
    background: transparent;
}

::-webkit-scrollbar-thumb:hover {
    background: #c2c2c2;
}

::-webkit-scrollbar-thumb:active {
    background: #919191;
}

::-webkit-scrollbar-thumb:start {
    background: red;
}

::-webkit-scrollbar-thumb:window-inactive {
    background: #a3a3a3;
}

::-webkit-scrollbar-track:hover,
::-webkit-scrollbar-track:active {
    background: #e0e0e0;
}
